// Importando o React
import React from "react";
// Importando os components necessários da lib react-materialize
import { Row, Col, Card } from 'react-materialize';


import UserProfile from '../user_profile/user_profile';

import Experience from '../experience/experience'
// Importando o avatar da empresa 1
import company_avatar from '../../images/company.png';
// Importando o avatar da empresa 1
import company_avatar2 from '../../images/company2.png';

import company_avatar3 from '../../images/company3.png';

const Home = () => (
  <Row>
    <Col m={3} s={12}>
      <UserProfile /> 
    </Col>
    <Col m={8} s={12}>
        <h5 className="subtitle">Sobre mim</h5>
        <Card>
          <div>
            <p><h5><b>Invictus</b></h5></p>
            <p>"Out of the night that covers me,<br></br>
                Black as the Pit from pole to pole,<br></br>
                I thank whatever gods may be<br></br>
                For my unconquerable soul.<br></br>

                In the fell clutch of circumstance<br></br>
                I have not winced nor cried aloud.<br></br>
                Under the bludgeonings of chance<br></br>
                My head is bloody, but unbowed.<br></br>

                Beyond this place of wrath and tears<br></br>
                Looms but the Horror of the shade,<br></br>
                And yet the menace of the years<br></br>
                Finds and shall find me unafraid.<br></br>

                It matters not how strait the gate,<br></br>
                How charged with punishments the scroll<br></br>
                I am the master of my fate:<br></br>
                I am the captain of my soul."</p>
            <br/>
              </div>
        </Card>
        <h5 className="subtitle">Experiências</h5>
            <Experience title="Desenvolvedor WEB"
                        company="DMA"
                        description="Desenvolvedor WEB dos sistemas do DMA."
                        avatar={company_avatar}
            />
            <Experience title="Professor de informática"
                        company="NEAD"
                        description="Professor de informática e inclusão digital."
                        avatar={company_avatar2}                    
            /> 
            <Experience title="Gerente de Comunicação"
                        company="NoBugs"
                        description="Gerente de comunicação e desenvolvedor WEB."
                        avatar={company_avatar3}                    
            /> 
    </Col>
  </Row>
);

export default Home;